html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #ffffff;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/** Media Queries **/
h1, h2, .heading-blanco, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-align: center;
  line-height: 1.5;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #5e311e;
}
h2 span, .heading-blanco span {
  color: #c39c63;
  font-size: 1.8rem;
  display: block;
}
h2::after, .heading-blanco::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-size: 10rem;
}

.heading-blanco {
  color: #ffffff;
}
.heading-blanco span {
  color: #ffffff;
}
.heading-blanco::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0;
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}

.header-inicio {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), -webkit-image-set(url(../img/header_inicio.avif) 1x, url(../img/header_inicio.webp) 1x, url(../img/header_inicio.jpg) 1x);
}

.header-nosotros {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), -webkit-image-set(url(../img/header_nosotros.avif) 1x, url(../img/header_nosotros.webp) 1x, url(../img/header_nosotros.jpg) 1x);
}

.header-proceso {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), -webkit-image-set(url(../img/header_proceso.avif) 1x, url(../img/header_proceso.webp) 1x, url(../img/header_proceso.jpg) 1x);
}

.header-menu {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), -webkit-image-set(url(../img/header_menu.avif) 1x, url(../img/header_menu.webp) 1x, url(../img/header_menu.jpg) 1x);
}

.header-galeria {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), -webkit-image-set(url(../img/header_galeria.avif) 1x, url(../img/header_galeria.webp) 1x, url(../img/header_galeria.jpg) 1x);
  background-position: center top;
}

.header-contacto {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), -webkit-image-set(url(../img/header_contacto.avif) 1x, url(../img/header_contacto.webp) 1x, url(../img/header_contacto.jpg) 1x);
}

.logo {
  max-width: 13rem;
  z-index: 11;
}
@media (min-width: 1200px) {
  .logo {
    max-width: 20rem;
  }
}

.contenido-header h1 {
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 8rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    margin-top: 10rem;
  }
}

.footer {
  background-color: #5e311e;
}
.footer .contenido-footer {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer .copyright {
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: #ffffff;
}

.footer-box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 8rem;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Bona Nova", serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #c39c63;
}
.footer-box p, .footer-box a {
  font-size: 2rem;
  margin: 0;
}
.footer-box a {
  color: #5e311e;
  font-weight: bold;
}

.barra {
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
}

.barras {
  color: #ffffff;
  font-size: 3rem;
  cursor: pointer;
  z-index: 11;
}

@media (min-width: 1200px) {
  .barras {
    display: none;
  }
}
.menu-nav {
  display: flex;
  gap: 2rem;
  text-transform: uppercase;
  font-size: 1.8rem;
}

.menu-nav__enlace {
  font-weight: 700;
}
.menu-nav__enlace:hover, .menu-nav__enlace.activo {
  color: #c39c63;
}

@media (max-width: 1200px) {
  .nav-principal {
    height: 0px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100vw;
    background-color: rgba(22, 18, 34, 0.9);
    transition: 0.5s;
    overflow: hidden;
  }
  .nav-principal.active {
    height: 100vh;
  }
  .menu-nav {
    display: flex;
    flex-direction: column;
    width: fit-content;
    margin: 8rem auto 0 auto;
    text-align: center;
    transition: 0.5s;
    opacity: 0;
    font-size: 3rem;
    gap: 3rem;
  }
  .menu-nav__enlace {
    padding: 1rem 2rem;
  }
  .menu-nav__enlace:hover {
    width: 80vw;
    display: inline-block;
    border-radius: 0.7rem;
    background-color: rgba(109, 107, 107, 0.3);
  }
  .nav-principal.active .menu-nav {
    opacity: 1;
  }
}
.contenido-principal {
  background-color: #ffffff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding: 10rem;
    margin-top: -10rem;
    z-index: 1;
  }
}

.iconos {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .listado-iconos {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 5rem;
  }
}
.listado-iconos li {
  display: flex;
  flex-direction: column;
}
.listado-iconos li img {
  max-height: 20rem;
}
@media (min-width: 768px) {
  .listado-iconos li img {
    max-height: 10rem;
  }
}
.listado-iconos li p {
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #5e311e;
  margin: 0 0 5rem 0;
}

.galeria {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 992px) {
  .galeria {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.galeria .imagen img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 992px) {
  .galeria .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria .imagen:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria .imagen:nth-child(2) img {
    width: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria .imagen:nth-child(4) img {
    height: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(5), .galeria .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(5) img, .galeria .imagen:nth-child(6) img {
    width: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria .imagen:nth-child(7) img {
    height: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria .imagen:nth-child(8) img {
    width: 94rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria .imagen:nth-child(9) img {
    width: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(10) img {
    width: 62rem;
    object-position: center;
  }
}

.nuestro-menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 5rem 20rem 5rem;
}
@media (min-width: 768px) {
  .nuestro-menu {
    background-size: 140%, 140%, 100%;
  }
}
@media (min-width: 992px) {
  .nuestro-menu {
    background-size: 50%, 50%, 100%;
  }
}
@media (min-width: 768px) {
  .nuestro-menu {
    padding: 15rem 0 20rem 0;
  }
}

@media (min-width: 768px) {
  .grid-menu {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.menu h3 {
  color: #ffffff;
}
.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu li p {
  color: #ffffff;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 4rem 0;
}
.menu li p span {
  display: block;
  color: #c39c63;
}
.menu li .precio {
  font-size: 4rem;
  font-weight: 700;
}

.nuestros-testimoniales {
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .nuestros-testimoniales {
    padding: 10rem;
  }
}
.nuestros-testimoniales .testimonial {
  background-color: #ebe7de;
  padding: 2rem 4rem;
  border-radius: 0.7rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  max-width: 50rem;
  margin: 0 auto;
}
.nuestros-testimoniales .testimonial .autor {
  text-align: right;
  font-size: 2.5rem;
  color: #5e311e;
  font-family: "Bona Nova", serif;
  font-style: italic;
}

.contenido-nosotros {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 992px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-nosotros .imagen {
  margin-bottom: 5rem;
}
@media (min-width: 992px) {
  .contenido-nosotros .imagen {
    margin-right: 2rem;
    margin-bottom: 0;
  }
}
.contenido-nosotros .imagen img {
  transform: rotate(-6deg);
  border: 3rem solid #ffffff;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.contenido-nosotros .texto blockquote {
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5e311e;
  font-size: 2rem;
  color: #5e311e;
}

.proceso-cafe {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .proceso-cafe {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.proceso-cafe:last-of-type {
  margin-bottom: 0;
}
.proceso-cafe:nth-child(even) .texto {
  order: -1;
}
.proceso-cafe .imagen img {
  width: 100%;
}
.proceso-cafe .imagen, .proceso-cafe .texto {
  flex: 1;
}
.proceso-cafe .texto {
  margin-top: 1.5rem;
}
.proceso-cafe .texto h3 {
  margin: 0;
  text-align: left;
  color: #5e311e;
}
.proceso-cafe .texto h3::after {
  content: "";
  display: block;
  width: 35%;
  height: 3px;
  background-color: #c39c63;
}

.contenido-menu {
  background-image: -webkit-image-set(url(../img/cafe.avif) 1x, url(../img/cafe.webp) 1x, url(../img/cafe.jpg) 1x);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .contenido-menu {
    aspect-ratio: 3/4.7;
  }
}
@media (min-width: 768px) {
  .contenido-menu {
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-menu .texto p {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contenido-contacto {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    background-image: -webkit-image-set(url(../img/granos_cafe.avif) 1x, url(../img/granos_cafe.webp) 1x, url(../img/granos_cafe.jpg) 1x);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.formulario .campo {
  margin-bottom: 2rem;
}
.formulario .campo:last-of-type {
  margin-bottom: 0;
}
.formulario .campo label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.formulario .campo input:not([type=submit]), .formulario .campo select {
  width: 100%;
  padding: 1rem;
  border: 0.3rem solid #5e311e;
}
.formulario .campo input[type=submit] {
  background-color: #5e311e;
  width: 100%;
  color: #ffffff;
  padding: 1rem;
  border: none;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: background-color 0.2s ease-in-out, transform 0.2s ease-in-out, border-radius 0.2s ease-in-out;
}
.formulario .campo input[type=submit]:hover {
  cursor: pointer;
  background-color: #4b2718;
  transform: scale(1.02);
  border-radius: 0.7rem;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5e311e;
}

.bg-white {
  background-color: #ffffff;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.padding-bottom-0 {
  padding-bottom: 0;
}